import {createSlice, PayloadAction} from '@reduxjs/toolkit';
import {Exam, ExamKind, ExamSettings, MathTraining} from "../types/app";
import {Operation, SimpleTask} from "../types/simple-task";

function getRandomInt(max: number) {
    return Math.floor(Math.random() * max);
}


const slice = createSlice({
    name: 'math-training',
    initialState: {
        currentExam: undefined,
        results: [],
    } as MathTraining,
    reducers: {
        start(state: MathTraining, action: PayloadAction<ExamSettings>) {
            const {payload} = action;
            if (payload.kind === ExamKind.MULTI) {
                let tasks: Array<SimpleTask> = [];
                for (let i = 2; i < 10; i++) {
                    for (let j = i; j < 10; j++) {
                        tasks.push({
                            first: i,
                            second: j,
                            operation: Operation.MULTI,
                            result: i * j,
                            answer: '',
                            spent: undefined,
                        })
                    }
                }
                const exam: Exam = {
                    tasks,
                    currentTaskIndex: getRandomInt(tasks.length),
                    startDate: new Date(),
                    wrong: 0,
                    correct: 0,
                    rank: 0,
                    spent: 0,
                    kind: payload.kind
                };
                state.currentExam = exam;
            }
        },
        answer(state: MathTraining, action: PayloadAction<number>) {
            const {currentExam} = state;
            if (currentExam) {
                const task = currentExam.tasks[currentExam.currentTaskIndex];
                task.answer = action.payload;
                if (task.answer === task.result) {
                    currentExam.correct = currentExam.correct + 1;
                } else {
                    currentExam.wrong = currentExam.wrong + 1;
                }
                const tasks = currentExam.tasks.filter(item => item.answer === '');
                if (tasks.length === 0) {
                    currentExam.currentTaskIndex = -1;
                    currentExam.spent = new Date().getTime() - currentExam.startDate.getTime();
                    state.results.push(currentExam);
                } else {
                    const nextTaskIndex = getRandomInt(tasks.length);
                    const nextTask = tasks[nextTaskIndex];
                    currentExam.currentTaskIndex = currentExam.tasks.findIndex(item => item === nextTask);
                }
            }
        }
    },
    extraReducers: (builder) => {

    },
});

export const {
    start,
    answer,
} = slice.actions;

export default slice.reducer;
