import {SimpleTask} from "./simple-task";

export enum ExamKind {
    MULTI
}

export type Exam = {
    tasks: Array<SimpleTask>,
    currentTaskIndex: number,
    rank: number,
    wrong: number,
    correct: number,
    startDate: Date,
    spent: number,
    kind: ExamKind,
}

export type MathTraining = {
    results: Array<Exam>
    currentExam: Exam | undefined,
}

export type ExamSettings = {
    kind: ExamKind
}
