import * as React from "react";
import {Exam} from "../../types/app";
import {connect} from "react-redux";
import Filter2Icon from '@mui/icons-material/Filter2';
import Filter3Icon from '@mui/icons-material/Filter3';
import Filter4Icon from '@mui/icons-material/Filter4';
import Filter5Icon from '@mui/icons-material/Filter5';
import Filter6Icon from '@mui/icons-material/Filter6';
import Filter7Icon from '@mui/icons-material/Filter7';
import Filter8Icon from '@mui/icons-material/Filter8';
import Filter9Icon from '@mui/icons-material/Filter9';
import {AppDispatch, AppState} from "../../services/store";
import {Badge, Grid} from "@mui/material";

type Props = {
    exam: Exam | undefined,
}

function MultipleDetail(props: Props) {
    const {exam} = props;
    const badge: Array<string> = new Array<string>(8);
    const badgeColor: Array<any> = new Array<any>(8); // 'primary' | 'secondary' | 'default' | 'error' | 'info' | 'success' | 'warning'
    badge.fill('0');
    badgeColor.fill('primary');
    if (exam && exam.tasks) {
        exam.tasks.forEach(item => {
            if (item.answer !== '') {
                const badgeIndex = item.first - 2;
                const badgeSecondIndex = item.second - 2;
                if (item.first < item.second){
                    badge[badgeSecondIndex] = '' + (Number(badge[badgeSecondIndex]) + 1);
                    if (badgeColor[badgeSecondIndex]  !== 'warning') {
                        badgeColor[badgeSecondIndex] = item.answer === item.result ? 'success' : 'warning';
                    }
                }
                badge[badgeIndex] = '' + (Number(badge[badgeIndex]) + 1);
                if (badgeColor[badgeIndex]  !== 'warning') {
                    badgeColor[badgeIndex] = item.answer === item.result ? 'success' : 'warning';
                }
            }
        });
        return (<div className="multiple-summary">
            <Grid container spacing={3}>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[0]} color={badgeColor[0]}>
                        <Filter2Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[1]} color={badgeColor[1]}>
                        <Filter3Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[2]} color={badgeColor[2]}>
                        <Filter4Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[3]} color={badgeColor[3]}>
                        <Filter5Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[4]} color={badgeColor[4]}>
                        <Filter6Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[5]} color={badgeColor[5]}>
                        <Filter7Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[6]} color={badgeColor[6]}>
                        <Filter8Icon fontSize="large"/>
                    </Badge>
                </Grid>
                <Grid item xs={3}>
                    <Badge badgeContent={badge[7]} color={badgeColor[7]}>
                        <Filter9Icon fontSize="large"/>
                    </Badge>
                </Grid>
            </Grid>
        </div>);
    }
    return (
        <div/>
    )
}

const mapStateToProps = (state: AppState) => {
    return {
        exam: state.mathTraining.currentExam,
    };
}

const mapDispatchToProps = (dispatch: AppDispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps)(MultipleDetail);
