import * as React from "react";
import {Exam, ExamKind} from "../../types/app";
import {connect} from "react-redux";

import {AppDispatch, AppState} from "../../services/store";
import Box from "@mui/material/Box";
import {ResponsiveChartContainer} from "@mui/x-charts";
import {LinePlot} from '@mui/x-charts/LineChart';
import {BarPlot} from '@mui/x-charts/BarChart';
import {ChartsXAxis} from '@mui/x-charts/ChartsXAxis';
import {ChartsYAxis} from '@mui/x-charts/ChartsYAxis';
import {axisClasses} from '@mui/x-charts/ChartsAxis';

type Props = {
    exams: Array<Exam>,
}

function MultipleDetail(props: Props) {
    const {exams} = props;
    let chartExams = exams;
    if (exams.length > 5) {
        chartExams = exams.slice(-5);
    }
    const data: Array<string> = [];
    const correct: Array<number> = [];
    const wrong: Array<number> = [];
    chartExams.forEach(item => {
        data.push(`# ${data.length + 1}`);
        correct.push(item.correct)
        wrong.push(item.wrong);
    });
    console.log(data, correct, wrong);

    return (
        <Box sx={{width: '100%', maxWidth: 600}}>
            <ResponsiveChartContainer
                xAxis={[
                    {
                        scaleType: 'band',
                        data: data,
                        id: 'exams',
                        label: 'Занятия',
                    },
                ]}
                yAxis={[{id: 'answers'}]}
                series={[
                   /* {
                        type: 'line',
                        id: 'progress',
                        yAxisKey: 'answers',
                        data: correct,
                    },*/
                    {
                        type: 'bar',
                        id: 'correct',
                        yAxisKey: 'answers',
                        data: correct,
                    },
                    {
                        type: 'bar',
                        id: 'wrong',
                        yAxisKey: 'answers',
                        data: wrong,
                    },
                ]}
                height={400}
                margin={{left: 70, right: 70}}
                sx={{
                    [`.${axisClasses.left} .${axisClasses.label}`]: {
                        transform: 'translate(-25px, 0)',
                    },
                    [`.${axisClasses.right} .${axisClasses.label}`]: {
                        transform: 'translate(30px, 0)',
                    },
                }}
            >
                <BarPlot/>
                <LinePlot/>
                <ChartsXAxis axisId="exams" label="Занятия" labelFontSize={18}/>
                <ChartsYAxis axisId="answers" label="Правильные ответы"/>
            </ResponsiveChartContainer>
        </Box>
    );
}

const mapStateToProps = (state: AppState) => {
    return {
        exams: state.mathTraining.results.filter(item => item.kind === ExamKind.MULTI),
    };
}

const mapDispatchToProps = (dispatch: AppDispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps)(MultipleDetail);
