import {Button, LinearProgress, TextField, Typography} from "@mui/material";
import * as React from "react";
import {useState} from "react";
import {Exam, ExamKind} from "../../types/app";
import {connect} from "react-redux";
import {AppDispatch, AppState} from "../../services/store";
import {answer, start} from "../../services/slice";
import EmojiEventsOutlinedIcon from '@mui/icons-material/EmojiEventsOutlined';
import Badge from "@mui/material/Badge";
import IconButton from "@mui/material/IconButton";
import SentimentVeryDissatisfiedOutlinedIcon from '@mui/icons-material/SentimentVeryDissatisfiedOutlined';

type Props = {
    exam: Exam | undefined,
    start: () => void,
    answer: (value: number) => void,
}

function Multiple(props: Props) {
    const {exam, start, answer} = props;
    const [value, setValue] = useState<number | string>('');
    const [wrongAnswer, setWrongAnswer] = useState(false);

    if (!exam) {
        return (<Button onClick={start}>
            Вперед!
        </Button>);
    } else {
        if (exam.currentTaskIndex === -1) {
            let finishMessage;
            if (exam.wrong === 0)
                finishMessage = 'Круто! Ни одной ошибочки!';
            else if (exam.wrong < 5)
                finishMessage = `Так близко, всего ${exam.wrong} ошибки!`;
            else if (exam.wrong < 10) {
                finishMessage = `Надо поднажать. Было ${exam.wrong} ошибок!`;
            } else {
                finishMessage = `У тебя ${exam.wrong} ошибок. Не унывай!`;
            }
            return (
                <div className="exam-result">
                    <Typography align="center">
                        {finishMessage}
                        <br/>
                        <br/>
                        Ты справилась за {exam.spent/1000} секунд.
                        <br/>
                        <br/>
                    </Typography>
                    <Button onClick={start}>
                        Начать снова
                    </Button>
                </div>
            );
        }
        const task = exam.tasks[exam.currentTaskIndex];
        const sendAnswer = () => {
            answer(Number(value));
            setWrongAnswer(false);
            setValue('');
        }
        const keyPress = (e: any) => {
            if (e.keyCode === 13) {
                if (Number(value) === task.result) {
                    sendAnswer();
                } else {
                    setWrongAnswer(true);
                }
            }
        }
        const progress = 100 / exam.tasks.length * (exam.wrong + exam.correct);
        return (
            <div className="multiple-form">
                <div className="statistics">
                    <IconButton color="inherit">
                        <Badge badgeContent={exam.correct} color="success">
                            <EmojiEventsOutlinedIcon fontSize="large"/>
                        </Badge>
                    </IconButton>
                    <IconButton color="inherit">
                        <Badge badgeContent={exam.wrong} color="error">
                            <SentimentVeryDissatisfiedOutlinedIcon fontSize="large"/>
                        </Badge>
                    </IconButton>
                </div>
                <LinearProgress variant="determinate" value={progress} className="progress"/>
                {wrongAnswer ? (
                        <div className="wrong-answer">
                            <Typography variant="h3" className="question-answer" align="center">
                                {task.first} x {task.second} = {task.result}
                            </Typography>
                            <Button onClick={sendAnswer}>
                                Запомнила
                            </Button>
                        </div>
                    ) :
                    (<div className="ask-answer">
                        <Typography variant="h3" className="question" align="center">
                            {task.first} x {task.second} = ?
                        </Typography>
                        <TextField
                            id="answer"
                            label="Answer"
                            type="number"
                            variant="outlined"
                            className="answer-field"
                            onKeyDown={keyPress}
                            onChange={(e: any) => {
                                setValue(e.target.value)
                            }}
                            value={value}
                        />
                    </div>)
                }
            </div>
        );
    }
}

const mapStateToProps = (state: AppState) => {
    return {
        exam: state.mathTraining.currentExam,
    };
}

const mapDispatchToProps = (dispatch: AppDispatch) => {
    return {
        start: () => dispatch(start({
            kind: ExamKind.MULTI,
        })),
        answer: (value: number) => dispatch(answer(value)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Multiple);
