import {combineReducers, configureStore} from '@reduxjs/toolkit';
import mathTraining from './slice';
import {MathTraining} from "../types/app";

const rootReducer = combineReducers({mathTraining});

const STORAGE_NAME = 'MATH_TRAINING';

const storageValue = localStorage.getItem(STORAGE_NAME);

let preloadedState: MathTraining | undefined;

if (storageValue) {
    try {
        preloadedState = JSON.parse(storageValue);
    } catch (e) {
        preloadedState = undefined;
    }
}


const store = configureStore({
    reducer: rootReducer,
    preloadedState: preloadedState ? { mathTraining: preloadedState } : undefined,
    middleware: (getDefaultMiddleware) => getDefaultMiddleware({
        serializableCheck: false,
    }),
});


store.subscribe(() => {
    localStorage.setItem(
        STORAGE_NAME,
        JSON.stringify(store.getState().mathTraining),
    );
});

export type RootState = ReturnType<typeof rootReducer>
export type AppState = ReturnType<typeof store.getState>
export type AppDispatch = typeof store.dispatch

export default store;
